<script lang="ts" setup>
import { ElDialog } from 'element-plus'
import Form from '@/components/common/Form.vue'
import { ref, reactive, watch, ModelRef } from 'vue'
import { useI18n } from 'vue-i18n'
import { useUserInfoStore } from '@/store/user-data'
import { changeEmailApi, getValidCodeApi } from '@/api/user'
import Message from '@/utils/message'
import regExt from '@/utils/regext'

const visible: ModelRef<boolean | undefined> = defineModel('visible')

const { t, locale } = useI18n()
const userInfoStore = useUserInfoStore()
const ruleForm = reactive({
  email: '',
  verificationCode: '',
})

const handleEmailCountdown = () => {
  const countDownTime = localStorage.getItem('ahab_email_time')
  if (countDownTime) {
    let remainTime = Math.floor((+countDownTime + 60 * 1000 - new Date().getTime()) / 1000)
    if (remainTime > 0) {
      formConfig.value.email.buttonText = `${remainTime}s`
      formConfig.value.email.buttonDisabled = true
      const inter = setInterval(() => {
        if (remainTime <= 1) {
          clearInterval(inter)
          formConfig.value.email.buttonText = t('login.sendVfcCode')
          formConfig.value.email.buttonDisabled = false
          return
        }
        formConfig.value.email.buttonText = `${--remainTime}s`
      }, 1000)
    }
  }
}

const handleSendVerificationCode = async () => {
  try {
    console.log('send code')
    localStorage.setItem('ahab_email_time', '' + new Date().getTime())
    handleEmailCountdown()
    const res: any = await getValidCodeApi({ email: ruleForm.email })
    console.log(res, '===res')
    if (res.statusCode === 200) {
      Message.toast('验证码发送成功！')
    } else {
      Message.error(res.message)
    }
  } catch (error: any) {
    Message.error(error.message || error.data.message)
  }
}

const handleEmailBlur = (value: string) => {
  console.log('blur', value)
  if (value && regExt.email.test(value)) {
    formConfig.value.email.buttonDisabled = false
  } else {
    formConfig.value.email.buttonDisabled = true
  }
}
const initConfig = () => ({
  email: {
    placeholder: t('login.newEmail'),
    onBlur: handleEmailBlur,
    withButton: true,
    buttonText: t('login.sendVfcCode'),
    buttonDisabled: true,
    buttonClick: handleSendVerificationCode,
  },
  verificationCode: {
    placeholder: t('login.verificationCode'),
  },
})
const formConfig = ref(initConfig())
const initRule = () => ({
  email: [
    {
      required: true,
      message: t('login.emailNull'),
      trigger: 'blur',
    },
    {
      type: 'email',
      message: t('login.emailError'),
      trigger: 'blur',
    },
  ],
  verificationCode: [
    {
      required: true,
      message: t('login.vfcCodeNull'),
      trigger: 'blur',
    },
  ],
})
const rules = ref(initRule())
const handleClose = () => {
  visible.value = false
  ruleForm.email = ''
  ruleForm.verificationCode = ''
}
const handleSubmit = async () => {
  console.log('submit')
  try {
    const res: any = await changeEmailApi({
      oldEmail: userInfoStore.email,
      newEmail: ruleForm.email,
      code: ruleForm.verificationCode,
    })
    if (res.statusCode === 200) {
      Message.toast('修改成功！')
      userInfoStore.email = ruleForm.email
      handleClose()
    } else {
      Message.error(res.message)
    }
  } catch (error: any) {
    Message.error(error.message || error.data.message)
  }
}
watch(locale, () => {
  rules.value = initRule()
  formConfig.value = initConfig()
})
</script>
<template>
  <el-dialog :title="t('login.changeEmail')" width="360" :model-value="visible" :before-close="handleClose">
    <Form
      v-model:rule-form="ruleForm"
      :formConfig="formConfig"
      :rules="rules"
      :confirmText="t('confirm')"
      :resetText="t('reset')"
      :showReset="false"
      @submit="handleSubmit"
    />
  </el-dialog>
</template>
<style lang="scss" scoped></style>
