<script lang="ts" setup>
import { ElDialog } from 'element-plus'
import Form from '@/components/common/Form.vue'
import { ref, reactive, watch, ModelRef } from 'vue'
import { useI18n } from 'vue-i18n'
import { changeEmailApi } from '@/api/user'
import Message from '@/utils/message'
import regExt from '@/utils/regext'

const visible: ModelRef<boolean | undefined> = defineModel('visible')

const { t, locale } = useI18n()
const ruleForm = reactive({
  password: '',
  confirmPassword: '',
})

const validatePass = () => (_rule: any, value: any, callback: any) => {
  try {
    if (value === '') {
      callback(new Error(t('login.passwordNull')))
    } else if (!regExt.password.test(value)) {
      callback(new Error(t('login.passwordInvalid')))
    } else {
      if (ruleForm.confirmPassword !== ruleForm.password) {
        // callback(new Error(t('login.confirmPasswordError')))
        // if (!ruleFormRef.value) return
        // ruleFormRef.value.validateField('confirmPassword', () => null)
      }
      callback()
    }
  } catch (error) {
    console.log(error)
  }
}
const validatePass2 = () => (_rule: any, value: any, callback: any) => {
  if (value === '') {
    callback(new Error(t('login.confirmPasswordNull')))
  } else if (value !== ruleForm.password) {
    callback(new Error(t('login.confirmPasswordError')))
  } else {
    callback()
  }
}
const initConfig = () => ({
  password: {
    placeholder: t('login.password'),
    type: 'password',
  },
  confirmPassword: {
    placeholder: t('login.confirmPassword'),
    type: 'password',
  },
})

const formConfig = ref(initConfig())
const initRule = () => ({
  password: [
    {
      validator: validatePass(),
      trigger: 'blur',
    },
  ],
  confirmPassword: [
    {
      validator: validatePass2(),
      trigger: 'blur',
    },
  ],
})
const rules = ref(initRule())
const handleClose = () => {
  visible.value = false
  ruleForm.password = ''
  ruleForm.confirmPassword = ''
}
const handleSubmit = async () => {
  console.log('submit')
  try {
    const res: any = await changeEmailApi({
      password: ruleForm.password,
    })
    if (res.statusCode === 200) {
      Message.toast('修改成功！')
      handleClose()
    } else {
      Message.error(res.message)
    }
  } catch (error: any) {
    Message.error(error.message || error.data.message)
  }
}
watch(locale, () => {
  rules.value = initRule()
  formConfig.value = initConfig()
})
</script>
<template>
  <el-dialog :title="t('login.changePassword')" width="320" :model-value="visible" :before-close="handleClose">
    <Form
      v-model:rule-form="ruleForm"
      :formConfig="formConfig"
      :rules="rules"
      :confirmText="t('confirm')"
      :resetText="t('reset')"
      :showReset="false"
      @submit="handleSubmit"
    />
  </el-dialog>
</template>
<style lang="scss" scoped></style>
