<script setup lang="ts">
import { reactive, ref, watch } from 'vue'
import { useI18n } from 'vue-i18n'
import { useUserInfoStore } from '@/store/user-data'
import Avatar from '@/components/Avatar.vue'
import EmailDialog from '@/components/user/EmailDialog.vue'
import PasswordDialog from '@/components/user/PasswordDialog.vue'
import Message from '@/utils/message'
import { registerApi } from '@/api/user'
import { ElForm, ElFormItem, ElInput, ElCard } from 'element-plus'
import type { FormInstance } from 'element-plus'
const { t, locale } = useI18n()
const userInfoStore = useUserInfoStore()
const { nickname, gender, email, password } = userInfoStore
const userForm = reactive({
  nickname,
  gender,
  email,
  password: password || '**************',
})

const initRule = () => ({
  nickname: [
    {
      required: true,
      message: t('login.nameNull'),
      trigger: 'blur',
    },
    { min: 2, max: 20, message: t('login.nameError'), trigger: 'blur' },
  ],
})
const rules = ref(initRule())
const ruleFormRef = ref()

const requestSubmitForm = async () => {
  try {
    const res: any = await registerApi({
      nickname: userForm.nickname,
      gender: userForm.gender,
    })
    console.log(res, '===res')
    if (res.statusCode === 200) {
      Message.toast('注册成功！')
    } else {
      Message.error(res.message)
    }
  } catch (error: any) {
    Message.error(error.message || error.data.message)
  }
}

const submitForm = (ruleFormRef: FormInstance | undefined) => {
  if (!ruleFormRef) return
  try {
    ruleFormRef.validate((valid, fields) => {
      if (valid) {
        console.log('submit!')
        requestSubmitForm()
      } else {
        console.log('error submit!', fields)
      }
    })
  } catch (error) {
    console.log(error)
  }
}
const isVisible = ref(false)
const openEmailModel = () => {
  console.log('openEmailModel')
  isVisible.value = true
}
const isPasswordVisible = ref(false)
const openPasswordModel = () => {
  console.log('openPasswordModel')
  isPasswordVisible.value = true
}
watch(locale, () => {
  rules.value = initRule()
})
</script>
<template>
  <div class="profile-setting">
    <div class="profile-setting-title">{{ t('login.setting') }}</div>
    <el-card class="profile-setting-card">
      <Avatar class="profile-setting-avatar" />
      <div class="" style="flex: 1">
        <el-form
          ref="ruleFormRef"
          :rules="rules"
          class="profile-setting-form"
          :inline="true"
          label-position="top"
          label-width="auto"
          :model="userForm"
          @submit="submitForm"
        >
          <el-form-item :label="t('login.userName')" prop="nickname" class="profile-setting-form-item">
            <el-input v-model="userForm.nickname" />
          </el-form-item>
          <el-form-item
            :label="t('login.gender')"
            prop="gender"
            class="profile-setting-form-item"
            style="margin-right: 0"
          >
            <el-select v-model="userForm.gender" :placeholder="t('login.gender')" clearable>
              <el-option :label="t('login.male')" value="male" />
              <el-option :label="t('login.famale')" value="famale" />
              <el-option :label="t('login.secret')" value="" />
            </el-select>
          </el-form-item>
          <el-form-item :label="t('login.email')" prop="email" class="profile-setting-form-item">
            <el-input v-model="userForm.email" disabled>
              <template #append>
                <el-button type="primary" @click="openEmailModel">{{ t('login.changeEmail') }}</el-button>
              </template>
            </el-input>
          </el-form-item>
          <el-form-item
            :label="t('login.password')"
            prop="password"
            class="profile-setting-form-item"
            style="margin-right: 0"
          >
            <el-input v-model="userForm.password" type="password" disabled>
              <template #append>
                <el-button type="primary" @click="openPasswordModel">{{ t('login.changePassword') }}</el-button>
              </template>
            </el-input>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <div>
      <el-button type="primary" @click="submitForm(ruleFormRef)">{{ t('confirm') }}</el-button>
    </div>
    <EmailDialog v-model:visible="isVisible" />
    <PasswordDialog v-model:visible="isPasswordVisible" />
  </div>
</template>
